import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, VideosApi, ContainerApi } from '../../../../shared/loopback-sdk';
import { CookieService } from 'angular2-cookie/core';
import { Headers, Http, Response } from '@angular/http';
import { stringify } from 'querystring';
import { ShareService } from '../../../../providers/share-service/share-service';
import * as moment from 'moment'; //for date manupalation
declare var window: any;
declare var hostname: any;
declare var sdkPrefix;
@Component({
	templateUrl: 'edit-video.component.html'
})
export class EditVideoComponent {
	@ViewChild('name') name: NgModel;
	@ViewChild('duration') duration: NgModel;
	@ViewChild('priority') priority: NgModel;
	public id: number;
	display: boolean = false; result: any;
	submitted: boolean = false;
	videoTitle: any;
	busy: any;
	userDetails: any;
	rolename: any;
	durationVal: any = "";
	image: any;
	imgErrDisplay: boolean = false;
	loading: boolean = false;
	imgErr: any = '';
	data: any;
	allData: any = [];
	imgName:any;
	videoPriority:Number=0;
	videoUrl:any;
	loginFlagToken:any;
	videoImage:any;
	video_status:boolean = false;
	videoValid: boolean = false;
	durationValid : boolean = false;
	enableSubmit :boolean = true;
	constructor(private loopAuth: LoopBackAuth, private route:ActivatedRoute, private http: Http, private _cookieService: CookieService, private videoApi: VideosApi, private router: Router, private containerApi: ContainerApi, private share: ShareService) {
		this.userDetails = this.loopAuth.getCurrentUserData();
		this.rolename = this.userDetails.userDet.roles;
		this.id = this.route.snapshot.params['id'];
		this.loginFlagToken = this._cookieService.get(sdkPrefix+'id');
	}

	showDialog() {
		this.display = true;
	}

	ngOnInit(): void {
		var dataArrObj = {
			"id":this.id
		}
		this.loading = true;
		this.videoApi.getVideos(dataArrObj).subscribe(data=>{
			if(data){
				let result = data.result[0];
				this.videoTitle = result.title;
				this.durationVal = result.duration;
				this.videoPriority = result.priority;
				this.videoUrl = result.video_url;
				this.video_status = (result.status)?true:false;
				if(result.logo_image){
					this.videoImage = result.logo_image+'?access_token='+this.loginFlagToken;
				}
			}
			this.loading = false;
		},(err)=>{
			this.loading = false;
		});
	}

	videoValuechange(){
		var regExpYouTube = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=)([^#\&\?]*).*/;
		var regExpVimeo = /(?:http?s?:\/\/)?(?:www\.)?(?:vimeo\.com)\/?(.+)/g;
		if(regExpYouTube.test(this.videoUrl) || regExpVimeo.test(this.videoUrl)){
			this.videoValid = true;
		}else{
			this.videoValid = false;
		}
	}
	durationValuechange(){
		console.log("Number(this.durationVal)",this.durationVal.split(":"));
		let durationArr = this.durationVal.split(":");
		for (const key in durationArr) {
			if(parseInt(durationArr[key]) > 0){
				this.durationValid = true;
			}
		}
	}

	async submit() {
		this.submitted = true;

		var regExpYouTube = /^.*(youtu.be\/|v\/|u\/\w\/|embed\/|watch\?v=|\&v=)([^#\&\?]*).*/;
		var regExpVimeo = /(?:http?s?:\/\/)?(?:www\.)?(?:vimeo\.com)\/?(.+)/g;
	 
			if(regExpYouTube.test(this.videoUrl) || regExpVimeo.test(this.videoUrl)){
				this.videoValid = true;
			}else{
				this.videoValid = false;
			}

			let durationArr = this.durationVal.split(":");
			for (const key in durationArr) {
				if(parseInt(durationArr[key]) > 0){
					this.durationValid = true;
				}
			}
			

		//console.log(this.name.valid && this.priority.valid && this.videoValid && this.durationValid);
		if (this.name.valid && this.duration.valid && this.priority.valid && this.videoValid && this.durationValid) {
			let uploadFlag1: any = true;
			this.loading = true;
			//upload logo image
			if (this.allData != "" && this.allData != undefined) {
				uploadFlag1 = await this.uploadImage(this.allData[0]);
			}
			//function call to change base64 format data to Image
			var dataArrObj = {
				"title": this.videoTitle,
				"logo_image": this.imgName,
				"video_url" : this.videoUrl,
				"priority": this.videoPriority,
				"duration": this.durationVal,
				"status":(this.video_status)?1:0,
				"updated_at": moment().format("YYYY-MM-DD hh:mm:ss.SSS")
			}
			this.busy = this.videoApi.addEditVideos(dataArrObj, this.id).subscribe(
				data=>{
					this.loading = false;
					if(data){
						console.log(data);
						if(data.result.id > 0){
							this.result = 'success';
						}
						this.display=true;
					}else{
						this.display=false;
						this.result = 'Something went wrong.';
					}
				},
				err=>{
					console.log("err", err);
					this.loading = false;
				},
				()=>{

				}
			)
		}
	}

	goToListing() {
		// window.location.reload();
		this.router.navigate(["videos"]);
	}

	onFileChange(file) {
		console.log("file", file);
		this.imgErrDisplay = false;
		this.data = new FormData();
		var imageData = new FileReader();
		if (file.target.files[0]) {
			var fileSize = Math.round((file.target.files[0].size) / 1024);
			var imgExt = file.target.files[0].name.split(".");
			console.log("file.target.files[0].type",file.target.files[0].type);
			if (imgExt.length > 2) {
				this.imgErr = "Invalid Image Format / Image name";
				this.imgErrDisplay = true;
				this.enableSubmit = false;
			} else if (((file.target.files[0].type) != "image/jpg") && ((file.target.files[0].type) != "image/png") && ((file.target.files[0].type) != "image/jpeg")) {
				this.imgErr = "Invalid Image Format";
				this.imgErrDisplay = true;
				this.enableSubmit = false;
			} else if (fileSize > 7000) {//chk img size
				this.imgErr = "Invalid Image Size";
				this.imgErrDisplay = true;
				this.enableSubmit = false;
			} else {
				imageData.onload = (event: any) => {
					var img = new Image();
					img.src = event.target.result;
					if (img.width > 500 || img.height > 500) {
						this.imgErr = "Invalid Image Dimension. It needs to be of max 500*500";
						this.imgErrDisplay = true;
						this.enableSubmit = false;
					} else {
						this.enableSubmit = true;
						let targetFile = file.target.files[0];
						let fileExtension = '.' + targetFile['name'].split('.').pop();
						let filename = Math.random().toString(36).substring(7) + new Date().getTime() + fileExtension;
						this.data.append("file", file.target.files[0], filename);
						this.allData = [];
						this.allData.push(this.data);
					}
				}
				imageData.readAsDataURL(file.target.files[0]);
			}
		}
	}

	uploadImage(fdata) {
		return new Promise((resolve, reject) => {
			console.log("fdata", fdata);
			let fileUrl = hostname+"/api/containers/logo_image";
			//let fileUrl = "http://phapi.experiencecommerce.com:4500/api/containers/logo_image";
			let loginFlagToken = this._cookieService.get(sdkPrefix+'id');
			this.loading = true;
			this.http.post(fileUrl + '/upload?access_token='+loginFlagToken, fdata).subscribe(res => {
				var dataimg = res['_body'];
				dataimg = JSON.parse(dataimg);
				if(dataimg){
					let imgName = dataimg['result']['files']['file'][0]['name'];
					this.imgName = fileUrl+"/download/"+imgName;
					resolve(true);
				}else{
					resolve(false);
				}
			},err => {
				this.imgErr = "Image upload failed";
				//this.imgshow = this.defaultImg;
				this.imgErrDisplay = true;
				this.loading = false;
				resolve(false);
			},
			() => {

			});
		});
	}
}


